import React from 'react';
import { DEFAULT_THEME } from '../../libs/theme/default-theme';
import { Theme } from '../../libs/theme/theme-types';
import { getThemeVariables } from '../../libs/theme/theme-variables';

interface ThemePrerenderProps {
  theme?: Theme;
}

export const ThemeHead: React.FC<ThemePrerenderProps> = (props) => {
  const { theme = DEFAULT_THEME } = props;

  return (
    <style
      id="mg-theme-head"
      type="text/css"
      // eslint-disable-next-line react/no-danger
      dangerouslySetInnerHTML={{ __html: getThemeVariables(theme).toCSS() }}
    />
  );
};

ThemeHead.displayName = 'ThemeHead';
